









import { Component, Vue } from "vue-property-decorator";
import { Article } from "@/models/Article";

@Component({
  components: {
    ArticlePreview: () => import("@/components/ArticlePreview.vue")
  }
})
export default class News extends Vue {
  articles: Article[] = [
    new Article(
      1,
      "1",
      "Новий спосіб навчання",
      "Карина Гарлиєва",
      "Голова організації",
      "1",
      new Date().toLocaleString(),
      `
      Новий пристрій, який стимулює головний мозковий нерв, може допомогти людям вивчити нову мову.

Стимуляція гілок блукаючого нерва використовується вже більше 20-ти років для лікування таких станів, як епілепсія. Для цього проводиться імплантація електродів у шию.
Меттью Леонард та його колеги розробили електрод, який вкладається у вушну раковину та стимулює частину блукаючого нерва, який веде до вуха.

Вчені випробовували пристрій на 36-ти добровольцях. Їх нерви стимулювали в різний час, поки вони виявляли звуки мандаринскього діалекту, китайської мови, незнайомої для них. Оскільки нервова стимуляція непомітна, добровольці не знали коли вони отримують імпульс чи отримували вони його взагалі. Зазначимо, що 12-ть досліджуваних взагалі не отримували нервових стимулів.

Учасники, які отримали нервову стимуляцію, у середньому на 13% краще класифікували тони та досягли максимальної продуктивності вдвічі швидше, ніж ті, хто не отримували стимуляції.

Вважається, що стимуляція блукаючого нерва у правильний момент навчання допомагає звертати увагу на найважливіші моменти. Хоча пристрій поки протестовано лише на діалекті китайської, Леонард каже, що в принципі такий підхід можна застосувати до будь-якої мови.
      `,
      ["Мозок", "Блукаючий нерв", "Навчання", "Пам'ять"],
      0
    ),
    new Article(
      2,
      "2",
      "Генетична приватність",
      "Карина Гарлиєва",
      "Голова організації",
      "1",
      new Date().toLocaleString(),
      `
      <p>🎊1 серпня 2020 року медичний світ відзначав 100-річчя Генрієтти Лакс🎊
Не знаєте хто це? Ми з радістю розповімо 😊</p>
<p>У 1951 році фермерці з Америки діагностували агресивну форму раку шийки матки. І, звичайно, взяли біопсію для досліджень. Ніякої згоди у письмовому чи словесному вигляді у неї не брали, що було звичайною практикою на той час.
Жінка померла через кілька місяців після оголошення страшного діагнозу.</p>
<p>Джордж Гей, який завідував лабораторією тканинної культури, виявив, що клітини Лакс продовжували жити та розмножуватися у лабораторних умовах, це був перший такий випадок. Світу медицини та нам клітини відомі як “клітини HeLa” (Henrietta Lacks).
Доктор Гей вирощував клітини і продавав їх вченим.</p>
<p>Родина “матері сучасної медицини” навіть не знала про їх існування до 1973 року, коли дослідники лікарні Джона Хопкінса звернулися до її дітей за пробами крові, щоб дізнатися більше про клітини HeLa, однак ніяких грошей чи компенсації вони не отримали.
Ця історія привернула широку увагу громади у 2010 році, коли вийшла книга Ребекки Склоот «Безсмертне життя Генрієтти Лакс».</p>
<p>Сварки тривали і досягли свого піку в 2013 році, коли Європейська лабораторія молекулярної біології в Гейдельберзі, Німеччина, опублікувала геном HeLa без згоди родини Лакс.
Після довгих суперечок та судових процесів, сторони прийшли до компромісу під назвою “Угода про використання генома даних HeLa”.
Такий випадок родини Лакс став переломним у галузі біоетики. Зараз у більшості країн є специфічні правила та закони щодо інформованої згоди та конфіденційності для захисту пацієнтів.
Ось таким чином одна родина переписала правила декількох галузей сучасної медицини.</p>
      `,
      ["Рак", "Біопсія", "Геном", "Біоетика"],
      0
    )
  ];
}
